import {authMe} from "./auth-reducer";

const SET_INITIALIZE= 'SET-INITIALIZE';

let initialState = {
    initialized: false
};

const appReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_INITIALIZE: {
            return {
                ...state,
                initialized: true
            }
        }
        default:
            return state;
    }
};

export const initializedSuccesess = () => ({type: SET_INITIALIZE});

export const initializeApp = () => {
    return (dispatch) => {
        let promise = dispatch(authMe());

        Promise.all([promise]).then( () => {
            dispatch(initializedSuccesess());
        });
    }
};

export default appReducer;