import {profileAPI, usersAPI} from "../api/api";

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET-USER-PROFILE';
const SET_STATUS = 'SET_STATUS';
const DELETE_POST = 'DELETE-POST';

let initialState = {
    userData: {
        username: "Uladzislau Halukha",
        userstatus: "Hey, I am a bodybuilder)"
    },
    postsData: [
        {id: 1, likescount: 18, text: "My first post"},
        {id: 2, likescount: 22, text: "My second post"},
        {id: 3, likescount: 17, text: "My third post"},
        {id: 4, likescount: 31, text: "My fourth post"},
    ],
    newPostText: 'valdik is the best',
    profile: null,
    status: ''
};

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST: {
            return {
                ...state,
                postsData: [...state.postsData, {
                    id: 5,
                    likescount: 12,
                    text: action.newPost
                }],
                newPostText: '',
            };
        }
        case SET_USER_PROFILE: {
            return {
                ...state,
                profile: action.profile
            }
        }
        case SET_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }
        case DELETE_POST: {
            return {
                ...state,
                postsData: state.postsData.filter(p => p.id !== action.id)
            }

        }
        default:
            return state;
    }
};

export const addPost = (newPost) => {
    return {type: ADD_POST, newPost}
};

export const deletePost = (id) => {
    return {type: DELETE_POST, id}
};

export const setUsersStatus = (status) => {
    return {type: SET_STATUS, status}
};

const setUserProfile = (profile) => {
    return {type: SET_USER_PROFILE, profile}
};


export const getProfile = (userId) => {
    return async (dispatch) => {
        let data = await usersAPI.getProfile(userId);
        dispatch(setUserProfile(data));
    };
};

export const getStatus = (userId) => {
    return async (dispatch) => {
        let data = await profileAPI.getStatus(userId);
        dispatch(setUsersStatus(data));
    };
};

export const updateStatus = (status) => {
    return async (dispatch) => {
        let data = await profileAPI.updateStatus(status);
        if (data.resultCode === 0) {
            dispatch(setUsersStatus(status));
        }
    };
};

export default profileReducer;
