
const SEND_MESSAGE = 'SEND_MESSAGE';

let initialState = {
    messagesData: [
        {sender: "Pavel", lastmessage: "hey, you are cool", time: "19:30", id: "1"},
        {sender: "Alex", lastmessage: "Okay", time: "18:11", id: "2"},
        {sender: "Aleksandr", lastmessage: "nice joke", time: "12:30", id: "3"},
        {sender: "Eugenie", lastmessage: "will see tomorrow", time: "21:42", id: "4"}
    ],
    dialog: [
        {
            id: 1, messages: "Hi " /*+
                        "hello" +
                        "whats up"*/
        },
        {
            id: 2, messages: "Hi "/* +
                        "hello" +
                        "whats up"*/
        },
        {
            id: 3, messages: /*"Hi " +
                        "hello" +*/
                "whats up"
        },
        {
            id: 4, messages: /*"Hi " +
                        "hello" +*/
                "yea, its good"
        }
    ]
};

const messageReducer = (state = initialState, action) => {
    switch (action.type) {
        case SEND_MESSAGE: {
            return {
                ...state,
                dialog: [...state.dialog, {
                    id: 5, messages: action.newMessageBody
                }]
            };
        }
        default:
            return state;
    }
};

export const sendNewMessageCreator = (newMessageBody) => {
    return { type: SEND_MESSAGE, newMessageBody}
};

export default messageReducer;
