import React from 'react';
import './Navigation.css'
import {NavLink} from "react-router-dom";

const Navigation = () => {
    return (
        <section className="navigation">
            <NavLink to="/profile" className="navigation__item">My Page</NavLink>
            <NavLink to="/news" className="navigation__item">News</NavLink>
            <NavLink to="/messages" className="navigation__item">Messages</NavLink>
            <NavLink to="/users" className="navigation__item">Users</NavLink>
            <NavLink to="/groups" className="navigation__item">Groups</NavLink>
            <NavLink to="/Musics" className="navigation__item">Musics</NavLink>
        </section>
    )
};

export default Navigation;