import * as axios from "axios";

const instances = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
        "API-KEY": "db4cafa3-bc67-473e-854c-895570b46d2f"
    }
});

export const usersAPI = {
    getUsers: (currentPage, pageSize) => {
        return instances.get(`users?page=${currentPage}&count=${pageSize}`).then(response => {
            return response.data;
        })
    },

    postUser: (id) => {
        return instances.post(`follow/${id}`).then(response => {
            return response.data;
        })
    },
    deleteUser: (id) => {
        return instances.delete(`follow/${id}`).then(response => {
            return response.data;
        })
    },
    getProfile: (id) => {
        return profileAPI.getProfile(id);
    }
};

export const profileAPI = {
    getProfile: (id) => {
        return instances.get(`profile/${id}`).then(response => {
            return response.data;
        })
    },
    getStatus: (id) => {
        return instances.get(`profile/status/${id}`).then(response => {
            return response.data;
        })
    },
    updateStatus: (status) => {
        return instances.put(`profile/status/`, {status}).then(response => {
            return response.data;
        })
    }
};

export const authAPI = {
    getAuth: () => {
        return instances.get(`auth/me`).then(response => {
            return response.data;
        })
    },
    login: (email, password, rememberMe = false) => {
        return instances.post(`/auth/login`, {email, password, rememberMe}).then(response => {
            return response.data;
        })
    },
    logout: () => {
        return instances.delete(`/auth/login`).then(response => {
            return response.data;
        })
    }
};
