import React from 'react'
import '../Profile.css'
import Post from "./Post/Post";
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, required} from "../../../utilities/validators/validators";
import {Textarea} from "../../common/FormsControl/FormsControls";

const maxLength = maxLengthCreator(10);

const ProfilePostsForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <Field component={Textarea} name={"newPost"} className="addpost" placeholder={"What is on your mind?"}
                   value={props.newPostText} validate={[required, maxLength]}/>
            <button className="addbutton">
                Add post
            </button>
        </form>
    )
};

const ProfilePostsReduxForm = reduxForm({
    form: 'login'
})(ProfilePostsForm);

const ProfilePosts = React.memo(props => {

    let posts = props.posts.map((p) => <Post text={p.text} likescount={p.likescount}/>);

    const onAddPost = (values) => {
        props.addPost(values.newPost);
    };

    return (
        <section>
            <h2 className="posts__title">Users Posts:</h2>
            <div className="fl-row-j">
                <ProfilePostsReduxForm onSubmit={onAddPost}/>
            </div>
            {posts}
        </section>
    )
});

export default ProfilePosts;